.ServiceItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  max-width: 300px;
}

.ServiceItemImage {
  width: 100%;
  height: 0;
  padding-bottom: 75%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

.ServiceItemContent {
  background-color: #292929;
  border: 3px solid #cc4c4c;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
  width: 100%;
  max-width: 300px;
  height: 110px;
}

.ServiceItemTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.ServiceItemIcon {
  font-size: 36px;
  color: #cc4c4c;
  margin-bottom: 10px;
}

.ServiceItemText {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 10px;
}

.ServiceItemQuote {
  font-size: 12px;
  font-style: italic;
  color: #cc4c4c;
}
